.container {
  border-radius: 6px;
  min-width: 300px;
  padding: 5px;
  margin: 0 0 20px 0;
  background-color: rgb(247, 247, 247);
  -webkit-box-shadow: 1px 2px 4px 0px rgba(50, 50, 50, 0.75);
  -moz-box-shadow: 1px 2px 4px 0px rgba(50, 50, 50, 0.75);
  box-shadow: 1px 2px 4px 0px rgba(50, 50, 50, 0.75);
  position: relative;
}

.link {
  text-decoration: none;
  color: inherit;
}

.image {
  display: block;
  width: 280px;
  object-fit: cover;
}

.paragraph {
  margin: 0;
  padding: 0;
}

.name {
  composes: paragraph;
  font-weight: 500;
}

.icon {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: var(--first-main-color);
  position: absolute;
  top: -10px;
  right: -10px;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}
